import { useIdentity } from "../hooks/identity";
import { useState, useRef, useEffect } from "react";
import s from "../style/postForm.module.sass"

import markdown from "../helpers/markdown";
const md = markdown({ anchor: false, embeds: true, supressHeadings: true });

const MAX_TITLE_LEN = 100;

export function PostForm (props) {
    var [id, axios] = useIdentity();
    var activeUser = id.users[id.active];

    var [title, setTitle] = useState(props.title ||"");
    var [text, setText] = useState(props.text || "");
    const [loading, setLoading] = useState(false);
    const [failed, setFailed] = useState(false);
    const [textareaHeight, setTextAreaHeight] = useState(props.comment?"4em":"8em");

    var textareaRef = useRef();

    console.log(loading);
    useEffect(() => {
        if(textareaRef && textareaRef.current) setTextAreaHeight((height) => {
            
           /* if(props.heightChange) {
                props.heightChange(textareaRef.current.scrollHeight-(typeof height === "number" ? height : 100))
            }*/
            return textareaRef.current.scrollHeight;
        })
    }, [textareaRef]);

    function change(evt) {
        switch(evt.target.name) {
            case "title":
                return setTitle(evt.target.value);
            case "text":
                setTextAreaHeight(textareaRef.current.scrollHeight)
                return setText(evt.target.value);
        }
    }

    async function submit(evt) {
        evt.preventDefault();
        setLoading(true);
        await later(1000);
        console.log("Title:", title);
        console.log("Text:", text);

        if(!activeUser) {
            setLoading(false);
            return id.toggleModal();
        }
        
        if(props.edit) {
            var res = await props.onEdit(text);
            if(!res) setFailed(true);
            setLoading(false);
            return;
        }

        //Submit Form
        if((title.length>=1 && title.length<=MAX_TITLE_LEN )|| props.comment) {
            axios.post(!props.comment ? `/post/` : `/comment/${props.comment}`, {
                title, 
                text
            })
            .then(function (response) {
                if(props.close) props.close()
                //show post 
                if(props.rerender) props.rerender(response.data.entityId || response.data.id)
                setLoading(false);
            })
            .catch(function (error) {
                console.log(error);
                //console.log(error.response.data);
                if(props.rerender) props.rerender()
                setLoading(false);
            });
        } else {
            
            setLoading(false);
            if(props.rerender) props.rerender()
            //props.close()
            
        }
    }

    return (
        <>
            <form onSubmit={submit} className={s.postForm}>
                {!props.comment &&
                <div className={s.titleCounter} data-invalid={title.length<1 || title.length>MAX_TITLE_LEN}> 
                    <input 
                        type="text" 
                        className={s.title}
                        value={title}
                        onChange={change}
                        name="title" 
                        placeholder="The post's title" 
                        autoComplete="off"
                        disabled={loading || props.edit || props.locked}
                    />
                    <p>{title.length}/{MAX_TITLE_LEN}</p>
                </div>}
                
                <textarea 
                    name="text" 
                    value={text}
                    onChange={change}
                    placeholder={props.locked?"LOCKED, bitch":"Go ahead, unleash your creative Genius!" }
                    rows={props.comment?"2":"4"} 
                    autoComplete="off"
                    disabled={loading  || props.locked}
                    ref={textareaRef}
                    style={{height: textareaHeight+3+"px"}}
                ></textarea>
            
                <div className={`${s.postButton} ${props.locked && s.dull}`}><button 
                    type="submit" 
                    value="Post!"
                    data-known={!Boolean(activeUser)} 
                    disabled={loading  || props.locked}
                >{props.locked?"HAHA NO":"Post!"}</button></div>
                {props.edit && <button className={s.cancelButton}
                    value="Cancel"
                    disabled={loading}
                    onClick= {()=> {props.onEdit(null, true)}}
                >Cancel</button>}
                <div className={s.author}> 
                    as
                    <span data-known={Boolean(activeUser)} >
                        {activeUser ? activeUser.username : "<UNKNOWN>"}
                    </span>
                    {failed && <i style={{color: "red", marginLeft:"2em"}}>(an error occured)</i>}
                </div>
            
                
            </form>
        </>
    );
}

function later(delay) {
    return new Promise(function(resolve) {
        setTimeout(resolve, delay);
    });
}